import Vue from 'vue'
import Router from 'vue-router'
import Store from '../store/store'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior () {
    return { x: 0, y: 0 }
  },
  routes : [
  {
    path: '/',
    component: () => import('../layouts/main/Main.vue'),
    meta: { requiresAuth: true },
    children: [
      {
        path: '/',
        redirect: '/dashboard'
      },
      {
        path: '/dashboard',
        name: 'Dashboard',
        component: () => import('../views/pages/dashboard.vue'),
        meta : {
          active : 'Dashboard',
          breadcrumb : [
            { title: 'Dashboard'}
          ]
        }
      },

      // Application Route
      {
        path: '/application',
        name: 'Application',
        component: () => import('../views/pages/application.vue'),
        meta : {
          active : 'Application',
          breadcrumb : [
            { title: 'Application' }
          ]
        }
      },
      {
        path: '/application/add',
        name: 'Application New',
        component: () => import('../views/pages/application/add.vue'),
        meta : {
          active : 'Application',
          breadcrumb : [
            { title: 'Application', url:'/application' },
            { title: 'Add New' }
          ]
        }
      },
      {
        path: '/application/:id/edit',
        name: 'Application Edit',
        component: () => import('../views/pages/application/edit.vue'),
        meta : {
          active : 'Application',
          breadcrumb : [
            { title: 'Application', url:'/application' },
            { title: 'Edit' }
          ]
        }
      },
      {
        path: '/application/:id/detail',
        name: 'Application Detail',
        component: () => import('../views/pages/application/detail.vue'),
        meta : {
          active : 'Application',
          breadcrumb : [
            { title: 'Application', url:'/application' },
            { title: 'Detail' }
          ]
        }
      },
      // Category Route
      {
        path: '/category',
        name: 'Category',
        component: () => import('../views/pages/category.vue'),
        meta : {
          active : 'Category',
          breadcrumb : [
            { title: 'Category'},
          ]
        }
      },
      {
        path: '/category/add',
        name: 'Category New',
        component: () => import('../views/pages/category/add.vue'),
        meta : {
          active : 'Category',
          breadcrumb : [
            { title: 'Category', url:'/category' },
            { title: 'Add New' }
          ]
        }
      },
      {
        path: '/category/:id/edit',
        name: 'Category Edit',
        component: () => import('../views/pages/category/edit.vue'),
        meta : {
          active : 'Category',
          breadcrumb : [
            { title: 'Category', url:'/category' },
            { title: 'Edit' }
          ]
        }
      },
      {
        path: '/category/:id/detail',
        name: 'Category Detail',
        component: () => import('../views/pages/category/detail.vue'),
        meta : {
          active : 'Category',
          breadcrumb : [
            { title: 'Category', url:'/category' },
            { title: 'Detail' }
          ]
        }
      },
      {
        path: '/upload',
        name: 'Upload',
        component: () => import('../views/pages/upload.vue'),
        meta : {
          active : 'Upload',
          breadcrumb : [
            { title: 'Upload'},
          ]
        }
      },
      {
        path: '/setting',
        name: 'Setting',
        component: () => import('../views/pages/setting.vue'),
        meta : {
          active : 'Setting',
          breadcrumb : [
            { title: 'Setting' },
          ]
        }
      },
      
    ]
  },
  {
  path: '',
  component: () => import('../layouts/full-page/FullPage.vue'),
    children : [
      {
        path: '/login',
        name: 'Login',
        component: () => import('../views/Login.vue'),
        meta : {
        
        }
      },
      {
        path: '/error',
        name: 'notfound',
        component : () => import ('../views/Error.vue'),
        meta: {
        
        }
      }

    ]
  },
  // Redirect to 404 page, if no match found
  {
    path: '*',
    redirect: '/error'
  }
]
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (Store.state.userLogin == false) {
      next({
        path: '/login'
      })
    } else {
      next()
    }
  } else {
    next()
  }
})


export default router
