

const menuUser = [
    {
        name : 'Dashboard',
        url : 'dashboard',
        icon : 'el-icon-odometer',
    },
    {
        name : 'Application',
        url : 'application',
        icon : 'el-icon-eleme',
    },
    {
        name : 'Category',
        url : 'category',
        icon : 'el-icon-s-fold',
    },
    {
        name : 'Upload',
        url : 'upload',
        icon : 'el-icon-upload',
    },
    {
        name : 'Setting',
        url : 'setting',
        icon : 'el-icon-s-tools',
    }
    
]

const userDefaults = {

    id : 0,
    user : 'Alceo One',
    photo : require('../assets/user.png'),
    photoDefaults : require('../assets/logo.png'),
    role : 'admin',
    status : ''
}

const layoutConfig = {
    sideBarVerticalWidth : 250 
}

const a = () => { 
    if (!localStorage.getItem('userData')) {
        return userDefaults
    } else {
        return JSON.parse(localStorage.getItem('userData'))
    }
}  

const userLogin = () => {
    if (!localStorage.getItem('userData')) {
        return false
    } else {
        return true
    }
}

const state = {

    userDataActive : a(),
    userLogin : userLogin(),
    menu : menuUser,
    themesConfig : layoutConfig,
    windowWidth: null

}

export default state