<template>
<div id="app">
    <router-view />
</div>
</template>

<script>

export default {
  data () {
    return {
    }
  },
  created() {
    window.addEventListener("resize", this.myEventHandler);
  },
  destroyed() {
    window.removeEventListener("resize", this.myEventHandler);
  },
  methods: {
    myEventHandler() {
      this.$store.commit('UPDATE_WINDOW_WIDTH', window.innerWidth)
    }
  },
  mounted () {
    this.$store.commit('UPDATE_WINDOW_WIDTH', window.innerWidth)
  }
}
</script>
